import React from 'react';
import { Row , Col } from 'react-flexbox-grid';
import Colored from './../Colored';
import { Slides, Slide, Inner } from './styled';
import Button, { ButtonGroup } from './../Button';

export default () => (
  <div>
    <h2>   <Colored></Colored></h2>
    <p style={{ marginBottom: '40px' }}>
          .   <br/>     ,  .
    </p>
    <Slides>
      <Slide backward >
          <h6>    </h6>
      </Slide>
      <Slide center ><h5>    </h5></Slide>
      <Slide >
        <Inner>
          <h5>     I </h5>
          <Row>
            <Col xs={12} sm={6} md={7}>
              <small>
                    31  2018    8096,25,       I .
              </small>
            </Col>
            <Col xs={12} sm={6} md={5} >
              <ButtonGroup>
                <Button outline type="button"></Button>
                <Button outline small type="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                  &#8226; &#8226; &#8226;
                </Button>
              </ButtonGroup>
            </Col>
          </Row>
        </Inner>
        <Button footer wide type="button"></Button>
      </Slide>
    </Slides>
  </div>
);



